import React, { useState } from 'react';
import { Outlet } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Box, CssBaseline, Drawer, IconButton, List, ListItem, ListItemIcon, ListItemText, Divider, useMediaQuery } from '@mui/material';
import LoginIcon from '@mui/icons-material/Login';
import AnnouncementIcon from '@mui/icons-material/Announcement';
import MenuIcon from '@mui/icons-material/Menu';
import { useTheme } from '@mui/material/styles';

const drawerWidth = 240;

function Layout() {
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const [mobileOpen, setMobileOpen] = useState(false);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const drawer = (
        <Box sx={{ overflow: 'auto', mt: 8 }}> {/* 添加 mt: 8 确保内容不会与 AppBar 重叠 */}
            <List>
                <ListItem button component="a" href="/#/login" onClick={handleDrawerToggle}>
                    <ListItemIcon>
                        <LoginIcon />
                    </ListItemIcon>
                    <ListItemText primary="登录" />
                </ListItem>
                <ListItem button component="a" href="/#/announcement" onClick={handleDrawerToggle}>
                    <ListItemIcon>
                        <AnnouncementIcon />
                    </ListItemIcon>
                    <ListItemText primary="公告" />
                </ListItem>
            </List>
            <Divider />
            <List>
                {/* 其他菜单项 */}
            </List>
        </Box>
    );

    return (
        <Box sx={{ display: 'flex' }}>
            <CssBaseline />
            <AppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}>
                <Toolbar>
                    {isMobile && (
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            edge="start"
                            onClick={handleDrawerToggle}
                            sx={{ mr: 2 }}
                        >
                            <MenuIcon />
                        </IconButton>
                    )}
                    <Typography variant="h6" noWrap component="div">
                        PUI
                    </Typography>
                </Toolbar>
            </AppBar>

            {/* 临时抽屉，用于移动设备 */}
            <Drawer
                variant="temporary"
                open={mobileOpen}
                onClose={handleDrawerToggle}
                ModalProps={{
                    keepMounted: true, // Better open performance on mobile.
                }}
                sx={{
                    display: { xs: 'block', sm: 'none' },
                    '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
                }}
            >
                {drawer}
            </Drawer>

            {/* 永久抽屉，用于桌面设备 */}
            <Drawer
                variant="permanent"
                sx={{
                    display: { xs: 'none', sm: 'block' },
                    '& .MuiDrawer-paper': { width: drawerWidth, boxSizing: 'border-box' },
                }}
                open
            >
                {drawer}
            </Drawer>

            <Box
                component="main"
                sx={{ flexGrow: 1, bgcolor: 'background.default', p: 3, marginLeft: { sm: `${drawerWidth}px` } }}
            >
                <Toolbar />
                <Outlet />  {/* 渲染子路由 */}
            </Box>
        </Box>
    );
}

export default Layout;
